:root {
  --violet-hocarre: rgb(76, 2, 62);
  --beige: rgb(237, 213, 183);
  --light-blue: rgb(93, 202, 209);
  --electric-blue: rgb(69, 104, 255);
  --white: rgb(250, 250, 250);
  --light-gray: rgb(179, 179, 179);
  --target-section: rgba(201, 181, 197, 0.678);
}

:root {
  --base-font: "Roboto", sans-serif;
}

/*---------------------------------------------------
* RESETS AND BOILEPLATE STYLE
-----------------------------------------------------*/
* {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 0px;
  margin: 0px;
  font-family: var(--base-font);
  background-color: var(--white);
  line-height: 1.1;
  height: 100vh;
  scroll-behavior: smooth;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

/*
** RIPPLE EFFECT FOR ALL THE BUTTONS
*/
span.ripple {
  position: absolute;
  transform: scale(0);
  animation: ripple 600ms linear;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.7);
}

@keyframes ripple {
  to {
    transform: scale(4);
    opacity: 0;
  }
}
/*
    FLOATING EFFECT
*/
.floating {
  animation-name: floating;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-out;
  margin-left: 30px;
  margin-top: 5px;
}

@keyframes floating {
  0% {
    transform: translate(0, 0px);
  }
  50% {
    transform: translate(0, 20px);
  }
  100% {
    transform: translate(0, 0px);
  }
}
/*-----------------------------
*GLOBAL NAVBAR STARTS
 ------------------------------ */
.header {
  width: 100%;
  max-height: 80px;
}

.navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1rem 1.5rem;
  padding: 0;
  width: 100%;
  max-height: 60px;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  background-color: var(--white);
  box-shadow: 0px 0px 10px gray;
}

.navbar__logo {
  position: relative;
  left: 3.4375rem;
}

.logo {
  width: 11.875rem;
  height: 100%;
}

.menu__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu__item {
  margin: 0 2.1875rem;
}

.item__link {
  font-size: 1.125rem;
  font-weight: 700;
  color: black;
  padding-bottom: 10px;
  position: relative;
}

/* ====> hover effect -> underlined links */
.item__link.underlined:hover {
  color: var(--violet-hocarre);
}

.item__link.underlined::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 0.125rem;
  background-color: var(--violet-hocarre);
  transform: scaleX(0);
  transition: transform 0.4s ease;
}

.item__link.underlined:hover::before {
  transform: scaleX(1);
}

/* ====> signUp CTA button*/
button {
  outline: 0;
  position: relative;
  overflow: hidden;
  transition: background 400ms;
}

.sign-up-btn {
  border: 0.1875rem solid var(--violet-hocarre);
  color: var(--violet-hocarre);
  background-color: transparent;
  border-radius: 1.25rem;
  padding: 0.3125rem 1.5625rem;
  box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.3);
  font-weight: 700;
  cursor: pointer;
}

.sign-up-btn:hover {
  transition: 0.3s ease-out;
  background-color: var(--violet-hocarre);
  color: var(--white);
}

/* ------------------------------------
*GLOBAL NAVBAR ENDS
--------------------------------  */
/*--------------------------------
*HAMBURGER MENU STARTS
--------------------------------- */
.menu__hamburger {
  border: none;
  background-color: var(--white);
}

.menu__hamburger {
  display: none;
}

.menu__bar {
  display: block;
  width: 1.5625rem;
  height: 0.1875rem;
  margin: 0.3125rem auto;
  transition: all 0.3s ease-in-out;
  background-color: var(--violet-hocarre);
}

/*----------------------------------
*HAMBURGER MENU ENDS
-----------------------------------  */
/*----------------------------------
*MOBILE VERSION STARTS
-----------------------------------  */
@media only screen and (max-width: 54.0625rem) {
  .navbar {
    justify-content: space-between;
  }

  .menu__list {
    position: fixed;
    left: -1000%;
    top: 2.75rem;
    flex-direction: column;
    width: 100%;
    border-radius: 0.625rem;
    text-align: center;
    transition: 0.7s;
    background-color: var(--white);
  }

  .menu__list.menu--is-active {
    left: 0;
  }

  .menu__item {
    margin: 1.5625rem 0;
  }

  .menu__hamburger {
    display: block;
    cursor: pointer;
    margin-right: 1.875rem;
    justify-content: flex-end;
  }

  .menu__hamburger.menu--is-active .menu__bar:nth-child(2) {
    opacity: 0;
  }

  .menu__hamburger.menu--is-active .menu__bar:nth-child(1) {
    transform: translateY(8px) rotate(45deg);
  }

  .menu__hamburger.menu--is-active .menu__bar:nth-child(3) {
    transform: translateY(-8px) rotate(-45deg);
  }
}
/*----------------------------------
*HAMBURGER VERSION ENDS
-----------------------------------  */
.section-presentation {
  height: 75vh;
  width: 100%;
  margin-top: 3.75rem;
  background-color: rgba(56, 180, 189, 0.781);
  color: white;
}

.presentation-container {
  width: 100%;
  height: 100%;
  display: flex;
}

/*------------------------------------------------------------------------
* LEFT PART STARTS
--------------------------------------------------------------------------*/
.presentation__left {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.presentation__header,
.presentation__body,
.presentation-cta {
  width: 80%;
}

.presentation__header {
  font-size: calc(1.725rem + 1.2vw);
  line-height: 1.3;
}

.presentation-title,
.presentation-text {
  margin: 0.9375rem 0 0.9375rem 1.875rem;
}

.presentation__body {
  font-size: calc(1.2rem + 0.4vw);
  margin-bottom: 1.875rem;
}

.presentation-cta__button {
  border: none;
  background-color: var(--violet-hocarre);
  color: #fff;
  border-radius: 1.5625rem;
  padding: 0.75rem;
  font-weight: 600;
  cursor: pointer;
  font-size: calc(0.975rem + 0.175vw);
  position: relative;
  left: 5%;
  box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.5);
  transition: 0.25s;
}

.presentation-cta__button:hover {
  box-shadow: 0 0.5em 0.5em -0.4em var(--violet-hocarre);
  transform: translateY(-0.25em);
}

/*------------------------------------------------------------------------
* LEFT PART ENDS
--------------------------------------------------------------------------*/
/*------------------------------------------------------------------------
* RIGHT PART STARTS
--------------------------------------------------------------------------*/
.presentation__right {
  display: flex;
  place-content: center;
  width: 50%;
  height: 100%;
}

/*------------------------------------------------------------------------
* RIGHT PART ENDS
--------------------------------------------------------------------------*/
@media screen and (max-width: 56.5625rem) {
  .presentation-container {
    flex-direction: column-reverse;
    align-items: flex-start;
  }

  .presentation__left {
    height: 65%;
    width: 100%;
    justify-content: flex-start;
  }

  .presentation__right {
    height: 35%;
    width: 100%;
    align-items: flex-start;
  }

  .presentation__right img {
    height: 70%;
    width: 70%;
  }

  .presentation__header {
    width: 70%;
  }

  .presentation__body {
    font-size: 1.125rem;
    width: 70%;
  }

  .presentation-cta__button {
    left: 10%;
  }
}
/* ----- Just a little line up effect ----------- */
.line-up {
  animation: 1.7s animate-lineUp ease-out;
}

@keyframes animate-lineUp {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  20% {
    opacity: 0;
  }
  50% {
    opacity: 1;
    transform: translateY(0%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}
